@media (prefers-color-scheme: dark) {
body[appearance="default"] {
    --text: white;
    --text-contrast: white;
    --highlight: #004953;
    --background: #171717;
    --placeholder: #bdc1c6;
    --font-main: "Roboto";
    --font-title: "Open Sans";
}
}

@media (prefers-color-scheme: light) {
body[appearance="default"] {
    --text: black;
    --text-contrast: white;
    --highlight: #004953;
    --background: #d8dee9;
    --placeholder: #70757a;
    --font-main: "Roboto";
    --font-title: "Open Sans";
}
}

body[appearance="dark"] {
    --text: white;
    --text-contrast: white;
    --highlight: #004953;
    --background: #171717;
    --placeholder: #bdc1c6;
    --font-main: "Roboto";
    --font-title: "Open Sans";
}

body[appearance="light"] {
